/* Heights and Widths */

.h-screen{
    height: 100vh
}

.w-400{
    width:400px
}

input{
    height: 40px;
    border: 2px solid rgb(213, 12, 12);
    width: 100%;
    padding-left: 20px;
}

.card{
    border: 2px solid rgb(110, 105, 105);
    box-shadow: 0 0 2px rgb(103, 100, 100) ;
}

/* text-sizes */

.text-2xl{
    font-size: 30px;
}

.text-xl{
    font-size: 25px;
}

.text-lg{
    font-size: 20px;
}

.text-md{
    font-size: 18px;
}

/* buttons */

.primary-btn{
    background-color: var(--primary);
    color: white;
    border: none;
    cursor: pointer;
    padding: 10px 20px;
}

.secondary-btn{
    background-color: var(--secondary);
    color: white;
    border: none;
    cursor: pointer;
    padding: 10px 20px;
}

a{
    text-decoration: none !important;
    color: var(--primary) !important;
}